import React from 'react'
import { Form } from 'antd'
import LinkageSelect from '../index'

// 
const dataSource = [
  {
    value: 'Zhejiang',
    name: '',
    children: [
      {
        value: 'Hangzhou',
        name: '',
        children: [
          {
            value: 'Yuhang',
            name: ''
          },
          {
            value: 'Gongshu',
            name: ''
          }
        ]
      },
      {
        value: 'Ningbo',
        name: '',
        children: [
          {
            value: 'Ninghai',
            name: ''
          },
          {
            value: 'Xiangshan',
            name: ''
          }
        ]
      }
    ]
  },
  {
    value: 'Anhui',
    name: '',
    children: [
      {
        value: 'Wuhu',
        name: '',
        children: [
          {
            value: 'Wuwei',
            name: ''
          }
        ]
      },
      {
        value: 'Maanshan',
        name: '',
        children: [
          {
            value: 'Hexian',
            name: ''
          }
        ]
      }
    ]
  }
]

function LinkageSelectDemo(props) {
  const { form } = props

  return (
    <Form>
      <LinkageSelect form={form} dataSource={dataSource} />
    </Form>
  )
}

export default Form.create({ name: 'linkage' })(LinkageSelectDemo)
